nav {
  margin-top: 1rem;
  min-width: 10vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  h1 {
    cursor: pointer;
  }
  button {
    background: transparent;
    border: none;
    cursor: pointer;
    border: 2px solid rgb(225, 202, 255);
    padding: 0.5rem;
    transition: all 0.8s ease;

    &:hover {
      background: rgb(225, 202, 255);
      color: white;
    }

    &:focus {
      outline: none;
    }
  }
}

@media screen and (max-width: 768px) {
  nav {
    button {
      z-index: 10;
    }
  }
}
