* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1,
h2,
h3 {
  color: rgb(56, 56, 56);
}

body {
  font-family: "Lato", sans-serif;
}

h3,
h4 {
  font-weight: 400;
  color: rgb(70, 70, 70);
}

.App {
  transition: all 1s ease;
}

.library-active {
  margin-left: 35%;
}

@import "./Player.scss";
@import "./Song.scss";
@import "./Library.scss";
@import "./Nav";
